@font-face {
  font-family: Inter;
  src: url("Inter-VariableFont_slnt,wght.bf32e4dd.ttf") format("truetype-variations");
  font-weight: 400 800;
}

body {
  box-sizing: border-box;
  min-width: 1400px;
  color: #000;
  text-transform: none;
  font-kerning: none;
  cursor: default;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Inter, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

img {
  object-fit: cover;
}

*, :before, :after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

a[href="##"], .noa {
  cursor: not-allowed;
}

.visuallyhidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: none;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

