.site-nav__item--active > a {
  font-weight: bold;
}

.site-nav__item a:hover span, .site-nav__item a:focus span {
  border-bottom: 1px solid;
  padding-bottom: 4px;
  transition: all .21s ease-out;
}

.header__nav {
  flex-grow: 1;
}

.site-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
