}

.site-nav__item {
  display: flex;
  position: relative;
}

.site-nav__item > a {
  color: inherit;
  margin: 7px 51px 7px 0;
  padding: 10px;
  text-decoration: none;
}

.site-nav__sub {
  opacity: 0;
  transform-origin: 0 0;
  transition: transform .26s ease-in 10ms;
  position: absolute;
  top: 37px;
  left: 10px;
  transform: scaleY(0);
}

.site-nav__sub-list {
  min-width: 220px;
  background-color: #f2f2f2cc;
  flex-direction: column;
  margin: 0;
  padding: 5px 10px;
  list-style: none;
  display: flex;
  position: relative;
  overflow: hidden;
}

.site-nav__sub-list:after {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, #0000, #ffffff4d, #0000);
  transition-duration: .7s;
  transition-delay: .2s;
  position: absolute;
  top: -100%;
  left: 0;
}

.site-nav__item:hover .site-nav__sub-list:after {
  top: 100%;
}

.site-nav__item:hover .site-nav__sub, .site-nav__item:focus-within .site-nav__sub {
  opacity: 1;
  z-index: 2;
  transform: scaleY(1);
}

.site-nav__sub-link {
  margin: 6px 0;
  padding: 3px 0;
  font-size: 10px;
  text-decoration: none;
  display: flex;
}

.site-nav__sub-item--current > .site-nav__sub-link {
  letter-spacing: -.04em;
  font-weight: bold;
}

.site-nav__sub-link:hover, .site-nav__sub-link:focus {
  color: #ff8d31;
  font-weight: bold;
}

